* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  border: 1px solid rgba(33, 33, 33, 0.5);
    border-radius: 5px;
  }
  
  .country-list {
    list-style: none;
    padding: 0;
  }
  
  .country-item {
    display: flex;
  }
  
  .country-text {
    margin-left: 10px;
  }
  
  .bold-text {
    font-weight: 600;
  }
  
  .country-thumb {
    display: flex;
    align-items: center;
  }
  
  .country-name {
    font-size: 38px;
    font-weight: 700;
  
    margin: 0;
    margin-left: 20px;
  }
